/* -------------------------------------------------------------------------
 Form elements
---------------------------------------------------------------------------- */

input[type="text"], 
input[type="search"], 
input[type="submit"], 
input[type="checkbox"], 
button,
textarea {
	appearance: none;
	outline: none;
	border: none;
	border-radius: 0;
}

/* -------------------------------------------------------------------------
