---------------------------------------------------------------------------- */



/* -------------------------------------------------------------------------
 WYSIWYG
---------------------------------------------------------------------------- */

.wysiwyg {
  ul,
  ol,
  table {
	color: gray;
	font-size: 1.5rem;
	line-height: 1.5;
	padding-left: 2em;
	margin: 2em 0;

	@media (min-width: 45em) {
	  font-size: 1.5rem;
	}

	ol,
	ul {
	  margin: .5em 0;
	}
  }

  ul {
	list-style-type: disc;
  }

  ol {
	list-style-type: decimal;
  }

  blockquote {
	padding: 2em;
	background-color: gray;
	margin: 2em;
	color: white;
  }

  table {
	border-collapse: collapse;
  }

  td {
	border: 1px solid gray;
	padding: 1em;
  }

  h1 {
	margin-top: 0;
	margin-bottom: .5em;
  }

  img { 
	margin: 2em 0;
  }

  p {
	margin: 1em 0;
  }
 
}
